import { Component, OnInit, AfterViewInit } from '@angular/core';
import { AnimationOptions } from 'ngx-lottie';
import { CommonService } from '../common.service';
import { AnimationItem } from 'lottie-web';
import { interval, TimeInterval } from 'rxjs';

@Component({
  selector: 'app-content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.scss']
})
export class ContentComponent implements OnInit, AfterViewInit {

  public StartFireworks: boolean;
  public BANNERTEXT: string[][] = [];
  public BANNERFIREWORKS:boolean;
  public PASSCODE:string[] = [];
  public EASTEREGGVISIBLE: boolean = false;
  public ShowPassWord:boolean;
  public ShowNote: boolean;
  public ShowVideo: boolean;

  public AnimationsLists: AnimationItem[] = [];
  public leftEgg: AnimationItem;
  public rightEgg: AnimationItem;
  public isLeft:boolean;
  public isRight: boolean;

  public PassWord:string;

  public ManWalk: AnimationOptions;
  public EasterEgg: AnimationOptions;
  public Cartoon: AnimationOptions;

  constructor(private cs: CommonService) {

    this.BANNERTEXT.push(['H', 'A', 'P', 'P', 'Y']);
    this.BANNERTEXT.push(['B','I','R','T','H','D','A','Y']);
    this.BANNERTEXT.push(['J','A','N','A','N','I']);

    this.StartFireworks = true;

    // path: "assets/json/karam.json"
    // path: "assets/json/egg.json",
    // path: "assets/json/bluecar.json",
    // path: "assets/json/emoji.json",
    // path: "assets/json/poppers-test.json",
    
    this.ManWalk = {
      path: "assets/json/poppers-test.json",
      loop: true,
      autoplay: true
    }
    
    this.EasterEgg = {
      path: "assets/json/egg.json",
      loop: false,
      autoplay: false
    }

    this.Cartoon = {
      path: "assets/json/poppers-test.json",
      loop: true,
      autoplay: true
    }

  }

  ngOnInit() {
    let body = document.getElementById('body') as HTMLBodyElement;
    body.style.backgroundColor = '#020202';
  }

  ngAfterViewInit() {
    this.startPhotos();
    setInterval(()=>{
      let arrow = document.getElementById("downarrow") as HTMLElement;
      if(arrow.style.bottom == "30px") arrow.style.bottom = "45px";
      else arrow.style.bottom = "30px";
    },500);
    setTimeout(()=>{
      this.BANNERFIREWORKS = true;
      let intervals = [];
      intervals.push(setInterval(()=>{
        let bannerText = document.getElementById("banner") as HTMLDivElement;
        bannerText.style.opacity = Math.random()+"";
        bannerText.style.opacity = Math.random()+"";
      },50));
      
      setTimeout(()=>{
        let bannerText = document.getElementById("banner") as HTMLDivElement;
        intervals.forEach(_=> clearInterval(_));
        bannerText.style.opacity = "1";

        setTimeout(()=>{
          this.startPhotos();
        },3000);
      },1500);
    },800);
  }

  startPhotos(){
    this.cs.activateLayer("TOP");
    // this.cs.activateLayer("LEFT");
    setTimeout(()=>{this.BANNERFIREWORKS = false;},500);
  }

  onImageClick(no:string){
    switch(no){
      case '2':
      case '5':
      case '7':
        this.PASSCODE.push(no);
        break;
      default:
        this.PASSCODE = [];
        console.log("failed");
    }
    if(this.PASSCODE.length == 4){
      this.checkPasscode();
    }
    if(this.PASSCODE.length > 4) this.PASSCODE = [];
  }

  checkPasscode(){
    let flag = true;
    if(this.PASSCODE[0] == '2' && this.PASSCODE[1] == "5" && this.PASSCODE[2] == "2" && this.PASSCODE[3] == "7") flag = true;
    else flag = false;
    if(flag){
      console.log("passed");
      this.EASTEREGGVISIBLE = true;
    }else{
      this.PASSCODE = [];
      console.log("failed");
    }
  }

  onEggClick(side:string){
    if(side == 'left'){
      this.leftEgg.play();
      this.isLeft = true;
    }
    else if(side == 'right'){
      this.rightEgg.play();
      this.isRight = true;
    }
    if(this.isLeft && this.isRight){
      setTimeout(()=> {
        this.showPassWord();
      },1500)
    }
  }

  animationCreated(item,side){
    if(side == 'left') this.leftEgg = item;
    else if(side == 'right') this.rightEgg = item;
  }

  showPassWord(){
    this.ShowPassWord = true;
    this.EASTEREGGVISIBLE = false;
  }

  onEnter(){
    if(this.PassWord.toLowerCase() == 'janksh'){
      this.ShowNote = true;
    }
  }

  onPlayClick(){
    this.ShowPassWord = false
    this.ShowVideo = true;
  }
}
