import { Component, OnInit } from '@angular/core';
import { CommonService } from '../common.service';

@Component({
  selector: 'app-timer',
  templateUrl: './timer.component.html',
  styleUrls: ['./timer.component.scss']
})
export class TimerComponent implements OnInit {

  public days: number;
  public hours:number;
  public minutes:number;
  public seconds:number;
  public width:number;

  constructor(private cs:CommonService) { }

  ngOnInit() {
    const year = new Date().getFullYear();
    const bday = new Date(year, 6, 27).getTime();
    // let bday = new Date().setTime(new Date().getTime() + 1500);

    // countdown
    let interval = setInterval(() => {

      // get today's date
      const today = new Date().getTime();

      // get the difference
      const diff = bday - today;

      // math
      this.days = Math.floor(diff / (1000 * 60 * 60 * 24));
      this.hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      this.minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
      this.seconds = Math.floor((diff % (1000 * 60)) / 1000);
      if(new Date().getTime() >= bday){
        clearInterval(interval);
        this.cs.activateLayer("TOP");
        // this.cs.activateLayer("LEFT");
        this.cs.setIsTime(true);
      }
    }, 100);

    this.width = screen.width;
    if(this.width < 650){
      this.width = Math.abs(this.width - 650);
      this.width = this.width/650;
      document.body.style.zoom = (( 1 - this.width) * 100) + '%';
    }
  }

}
