import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class CommonService {

  private IsTime:boolean;
  public TopLayers: HTMLCollection;
  public BottomLayers: HTMLCollection;
  public LeftLayers: HTMLCollection;
  public RightLayers: HTMLCollection;
  constructor() { }

  setIsTime(val:boolean){ this.IsTime = val }
  getIsTime():boolean{ return this.IsTime }

  activateLayer(dir:string){
    let layers:HTMLCollection;
    switch(dir){
      case "TOP":
        layers = this.TopLayers;
        break;
      case "LEFT":
        layers = this.LeftLayers;
        break;
      case "RIGHT":
        layers = this.RightLayers;
        break;
      case "BOTTOM":
        layers = this.BottomLayers;
        break;
      default:
        layers = this.TopLayers;
    }
    for(let i=0;i<layers.length;i++){
      let layer = layers[i] as HTMLElement;
      layer.classList.toggle("active");
    }
  }

}
